.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  position: fixed;
  width: 96%;
  height: 90px;
  z-index: 10;
}

.header-bg {
  background-color: rgb(0, 0, 0, 0.85);
  transition: 0.5s;
}
.nav-menu {
  display: flex; 
}

.nav-menu li {
  padding: 0 1em;
}

.nav-menu li a {
  font-size: 1.5em;
  font-weight: 500;
}

.side-bar {
  display: none;
}

@media screen and (max-width: 1040px) {
  .nav-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.85)	;
    position: absolute;
    top: 0;
    right: -100%;
    z-index: -3;
    transition: 0.3s;
  }

  .nav-menu.active {
    right: 0;

  }

  .nav-menu li {
    padding: 1em 0;
  }

  .nav-menu li a {
    font-size: 2em;
  }

  .side-bar {
    display: initial;
  }
}